<script>
import config from "@/common/config";
import customTranslate from "@/common/customTranslate";
import utils from "@/common/utils.js";
import service from "@/common/service";
import moment from "moment";
import BpmnJS from "bpmn-js/lib/Modeler";
import propertiesPanelModule from "bpmn-js-properties-panel";

import camundaProvider from "bpmn-js-properties-panel/lib/provider/camunda";
import camundaModdle from "@/moddle/camunda";

import activitiProvider from "bpmn-js-properties-panel/lib/provider/activiti";
import activitiModdle from "@/moddle/activiti";
//import diagramXML from  "@/assets/template.bpmn";

export default {
  // el: "#app",

  data() {
    return utils.createModel({
      processes: [],
      process: {},
      template: "",
      moment: moment,
      config: config,
      invalidName: false,
    });
  },
  created: function () {},
  mounted() {
    service.getProcessTemplate("template").then((response) => {
      this.template = response.data;
      this.refresh();
    });
  },
  methods: {
    refresh: function () {
      var id = this.$route.query && this.$route.query.id;
      this.process._id = id;
      this.invalidName = false;
      var sourceId = this.$route.query && this.$route.query.sourceId;
      return new Promise((resolve, reject) => {
        if (id) {
          service.getItem("process", id).then((process) => {
            resolve(process.data);
          });
        } else if (sourceId) {
          service.getItem("process", sourceId).then((process) => {
            process.data.name = "[] " + process.data.name;
            process.data.createdBy = "";
            process.data.createdAt = "";
            process.data.updatedBy = "";
            process.data.updatedAt = "";
            process.data._id = "";
            resolve(process.data);
          });
        } else {
          var engine =
            (this.$route.query && this.$route.query.engine) || "camunda";
          resolve({ engine });
        }
      }).then((data) => {
        this.process = data;

        this.loadBpmn();
      });
    },

    loadBpmn() {
      if (this.bpmn) {
        this.bpmn.destroy();
      }

      var extension;
      var provider;

      if (this.process.engine === "camunda") {
        extension = { camunda: camundaModdle };
        provider = camundaProvider;
      } else {
        extension = { camunda: activitiModdle };
        provider = camundaProvider;
      }

      var translate = {
        translate: ["value", customTranslate],
      };

      this.bpmn = new BpmnJS({
        additionalModules: [propertiesPanelModule, provider, translate],
        moddleExtensions: extension,
        container: "#canvas",
        propertiesPanel: {
          parent: "#properties",
        },
      });

      var xml = this.process.xml || this.template;

      return this.bpmn.importXML(xml);
    },
    changeEngine() {
      var target = this.process.engine;
      var source = target === "camunda" ? "activiti" : "camunda";
      var xml = this.process.xml;

      var scripts = {
        namespace: {
          camunda: 'xmlns:camunda="http://camunda.org/schema/1.0/bpmn"',
          activiti: 'xmlns:camunda="http://activiti.org/bpmn"',
        },
        // prefix: {
        //   camunda: "camunda:",
        //   activiti: "activiti:"
        // }
      };

      for (var name in scripts) {
        var mapping = scripts[name];
        var regex = new RegExp(mapping[source], "g");
        xml = xml.replace(regex, mapping[target]);
      }
      console.log(xml);
      this.process.xml = xml;
      this.loadBpmn();
    },
    deleteProcess() {
      if (!this.process._id) {
        return;
      }
      service.deleteItem("process", this.process._id).then((result) => {
        //this.refresh();
        $("#deleteModal").modal("hide");
        this.$router.push({
          name: "processes",
        });
      });
    },

    saveProcess() {
      this.invalidName = false;
      if (!this.process.name) {
        this.invalidName = true;
        return;
      }
      var process = JSON.parse(JSON.stringify(this.process));
      delete process._etag;

      var stamp = moment().format("x");
      if (!process._id) {
        process.type = "process";
        //  process.engine = this.engine;
        process.createdBy = this.user.id;
        process.createdAt = stamp;
        process._id = stamp;
      } else {
        // process.engine = this.engine;
        process.updatedBy = this.user.id;
        process.updatedAt = stamp;
      }
      var id = process._id;
      delete process.createdName;
      delete process.updatedName;
      this.bpmn
        .saveXML({ format: true })
        .then((response) => {
          process.xml = response.xml;
          return this.bpmn.saveSVG();
        })
        .then((response) => {
          process.svg = response.svg;
          return service.saveItem("process", process);
        })
        .then(
          (result) => {
            if (!this.process._id) {
              this.$router.push({
                name: "processEdit",
                query: { id: id },
              });
            }
            this.$parent.$emit("message", {
              type: "success",
              title: "",
              message: "",
            });
            this.refresh();
          },
          (result) => {
            this.invalidName = true;
            this.$parent.$emit("message", {
              type: "error",
              title: "",
              message: result.message,
            });
          }
        );
    },
    saveXML() {
      this.bpmn.saveXML({ format: true }).then((response) => {
        var xml = response.xml;
        var name = this.process.name + ".bpmn20.xml"; //"diagram.bpmn";
        var dom = document.createElement("a");
        var bb = new Blob([xml], {
          type: "text/xml",
        });

        dom.setAttribute("href", window.URL.createObjectURL(bb));
        dom.setAttribute("download", name);

        dom.dataset.downloadurl = ["text/xml", dom.download, dom.href].join(
          ":"
        );
        dom.draggable = true;
        dom.classList.add("dragout");

        dom.click();
      });
    },
  },
};
</script>

 
